body{
  margin: 0px;
  min-height: 100vh;
}

.App{
  background-color: #282c34;
  min-height: 90vh;
  font-size: calc(10px + 2vmin);

  background-image: url("./assets/4528659-cat-girl-cleavage-short-hair-rem-re-zero-rezero-kara-hajimeru-isekai-seikatsu-nekomimi-aqua-eyes-aqua-hair-blushing-headdress-maid-simple-background.jpg");
  
  background-size: cover;
  
  padding: 0px 50px;

  display: flex;
  flex-direction: column;
  justify-content: center;
}

.App .heading h3{
  margin: 0px;
  font-weight: 400;
}

.App .content{
  padding: 0px 700px 0px 0px;
}

.App .content p{
  font-weight: 300;
}

.App .CTA button{
  
  background-color: #ffe1e1;
  color: #c72742;
  border: 2px solid #c72742;

  font-size: 23px;
  border-radius: 110px;
  padding: 6px 34px;


}

.App .CTA button:hover{
  background-color: #c72742;
  color: #ffe1e1;
  border: 2px solid #c72742;
}

.Products{
  background-color: white;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Products li{
  color: black;
  padding: 20px;
  box-shadow: 0px 0px 10px 0px grey;
  border-radius: 10px;
}

.productUl li img{
  height: 300px;
  width: auto;
}

.productUl li button{
  background-color: #ffe1e1;
  color: #c72742;
  border: 3px solid #c72742;
  border-radius: 100px;
  padding: 3px 6px;

}

.productUl li button:hover{
  background-color: #c72742;
  color: #ffe1e1;
  border: 3px solid #c72742;
}

.Nav{
  display: flex;
  justify-content:space-between;
  align-items: center;
  min-height: 10vh;
  color: white;
  background-color: #ff647e;
  padding: 0px 50px;
}

.Nav h1{
  color: white;
}

ul{
  display: flex;
  justify-content: space-around;
  gap: 50px;
  list-style-type: none;
}

.productUl{
  margin:0px;
  padding:50px 100px ;
  display: grid;
  grid-template-columns: 1fr 1fr ;
  gap: 50px;
  list-style-type: none ;
}

li{
  color: white;
  text-align: center;
  border: 2px solid white;
  border-radius: 20px;
  padding: 0px 10px;
}

li:hover{
  color: #ff647e;
  background-color: white;
}

.Shop{
  background-color: white;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: black;
  padding: 20px;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.shopUl{
  margin:0px;
  padding:0px ;
  display: flex;
  flex-direction: column;
  gap: 20px;
  list-style-type: none ;;
}

.shopUl li{
  color:black;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 10px;
  box-shadow: 0px 0px 7px 0px grey;
  border-radius: 10px;
  width: 400px;
  
}

.shopUl li .description p{
  font-size: 15px;
  margin: 0px ;
}

.shopUl li .description button{
  background-color: #ffe1e1;
  color: #c72742;
  border: 2px solid #c72742;
  border-radius: 100px;
}

.shopUl li .description button:hover{
  background-color: #c72742;
  color: #ffe1e1;
  border: 2px solid #c72742;
}

.shopUl li .quantity{
  border: 1px solid black;
}

.shopUl li .quantity p{
  font-size: 20px;
}

.shopUl li .quantity button{
  background-color: white;
  border: 0px ;
}

.shopUl li img{
  height: 100px;
  width: auto;
}

.quantity{
  display: flex;
  justify-content: space-evenly;
}

.quantity p{
  margin: 0px;
}

a{
  text-decoration: none;
}

.finalBill{
  display: flex;
  flex-direction: column;
  padding: 20px 10px;
  margin-bottom: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 7px 0px grey;
}

.shipping{
  padding-bottom: 10px;
  border-bottom: 2px solid black;
}

.total{
  padding: 5px 0px 5px 0px;
}

.subTotal, .shipping{
  font-size: 15px;
}

.subTotal h3, .shipping h3, .subTotal p, .shipping p{
  font-weight: 300;
}

.subTotal, .shipping, .total{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 400px;
}

.finalBill h3, .finalBill p{
  margin: 0px;
}

.finalBill button{
  background-color: #FF6138;
  color: white;
  border: 2px solid #FF6138;
  border-radius: 100px;
  font-weight: bold;
}

.finalBill button:hover{
  background-color: #ff3300;
}